<template>
		<!--表情-->
		<view v-show="show" class="emoji" @tap.prevent.stop="">
			<view class="list">
				<view class="item" v-on:click="fuckemoji(item)" v-for="(item, index) in emojis" :key="index">{{ item }}</view>
			</view>
		</view>
</template>

<script>
var emoji = require('./emo.json')

export default{
	components: {  },
	props:['show'],
	data(){
		return {
			emojis: [],
			content:"haha",
		}
	},
	mounted() {
		
		this.emojis = emoji
	},
	methods: {
		fuckemoji(item){
			this.$store.commit("set_emoji", item)
		}
	}
}
</script>

<style lang="scss" scoped>
.container{
	height: 100vh;
	overflow: hidden;
}

/* #ifdef H5 */
.container{
	height: calc(100vh - 88upx);
}
/* #endif */

.status_bar,
.container,
.header,
.emoji{
	background-color: #F3F3F3;
}
.header{
	border-bottom: 2upx solid #EEE;
	
	.center{
		font-weight: bold;
	}
}
.emoji{
	height: 400upx;
	padding: 0 0 20upx 20upx;
	position: relative;
	
	.list{
		width: 100%;
		height: 400upx;
		padding: 20upx 0;
		overflow-y: auto;
		
		.item{
			float: left;
			display: block;
			height: 60upx;
			line-height: 60upx;
			width: calc(100% / 12);
			margin-right: 20upx;
		}
	}
}

</style>
